'Alexander Graham Bell Building',
'kater House',
'Alrick Building',
'Anatomical Museum',
'Ann Walker Building',
'Anne Rowling Regenerative Neurology Clinic',
'Appleton Tower',
'Arcadia Nursery',
'kater House'
],
addData: [
'kater House',
'Alrick Building',
'Anatomical Museum',
'Ann Walker Building',
'Anne Rowling Regenerative Neurology Clinic',
'Appleton Tower',
'Arcadia Nursery',
'Argyle House',
'Abden House',
'Alexander Graham Bell Building'
],
onFetching: false
};
},
methods: {
...mapActions(['loadPageConfig']),
onItemClick (item, index) {
this.index2 = index;
this.demo = item;
},
onScrollBottom () {
if (this.onFetching) {

} else {
this.onFetching = true;
setTimeout(() => {
this.bottomCount = Array.concat(this.bottomCount, this.addData);
this.onFetching = false;
}, 2000);
}
}
},
components: {
XHeader,
Tab,
TabItem,
Flexbox,
FlexboxItem,
XImg,
Swiper,
SwiperItem,
Scroller,
LoadMore
},
store
};



